.navigation-mobile {
  position: fixed;
  width: 320px;
  height: 100%;
  top: 0;
  overflow-y: auto;
  overflow-x: hidden;
  opacity: 0;
  visibility: hidden;
  z-index: 9999;
  transition-delay: 300ms;
  left: 0;
  .xs-block({
    width: 100%;
    });
  .show {
    width: 5%;
    right: 0;
    cursor: pointer;
    margin: 16px 10px 0 0;
    position: absolute;
    z-index: 99999999;
  }
  .nav {
    display: block;
  }
  &.active {
    opacity: 1;
    visibility: visible;
    transition-delay: 0s;
    .navigation__inner {
      background-color: #065279;
      -webkit-transform: translate(0, 0);
              transform: translate(0, 0);
      transition: background-color 0s linear 599ms, -webkit-transform 300ms linear;
      transition: transform 300ms linear, background-color 0s linear 599ms;
      transition: transform 300ms linear, background-color 0s linear 599ms, -webkit-transform 300ms linear;
      &:after {
        width: 300%;
        border-radius: 50%;
        -webkit-animation: elastic 150ms ease 300.5ms both;
              animation: elastic 150ms ease 300.5ms both;
      }
    }
  }
  .navigation__inner {
    position: absolute;
    width: 320px;
    height: 100%;
    top: 0;
    left: 0;
    overflow: hidden;
    z-index: 999999;
    -webkit-transform: translate(-100%, 0);
            transform: translate(-100%, 0);
    transition: background-color 0s linear 300ms, -webkit-transform 300ms linear;
    transition: transform 300ms linear, background-color 0s linear 300ms;
    transition: transform 300ms linear, background-color 0s linear 300ms, -webkit-transform 300ms linear;
    background-color: #065279;
      ul {
        list-style: none;
        background-color: #065279;
        li {
          padding-left: 30px;
          padding-right: 30px;
          .active a:after {
            width: 100%;
            margin-left: 0;
          }
          a {
            color: #d5d5d5;
            font-size: 18px;
            line-height: 2;
          }
          &:after {
              display: block;
              content: "";
              height: 1px;
              width: 0%;
              background-color: #fff;
              -webkit-transition: width 0.4s ease-in-out;
              transition: width 0.4s ease-in-out;
              margin-top: 2px;
            }
            &:hover {
              &:after {
                width: 100%;
                margin-left: 0;
              }
            }
        }
      }
