@fields_sm: 30px;
@fields_xs: 20px;
@fields_xxs: 20px;

.wrapper-full(){
    padding-left: @fields;
    padding-right: @fields;

    .lm-block({
        padding-left: @fields_lm;
        padding-right: @fields_lm;
    });

    .lsm-block({
        padding-left: @fields_lsm;
        padding-right: @fields_lsm;
    });

    .smd-block({
        padding-left: @fields_smd;
        padding-right: @fields_smd;
    });

    .mmd-block({
        padding-left: @fields_mmd;
        padding-right: @fields_mmd;
    });

    .xsmmd-block({
        padding-left: @fields_xsmmd;
        padding-right: @fields_xsmmd;
    });

    .md-block({
        padding-left: @fields_md;
        padding-right: @fields_md;
    });

    .xlmd-block({
        padding-left: @fields_xlmd;
        padding-right: @fields_xlmd;
    });

    .lmd-block({
        padding-left: @fields_lmd;
        padding-right: @fields_lmd;
    });

    .sm-block({
        padding-left: @fields_sm;
        padding-right: @fields_sm;
    });

    .xs-block({
        padding-left: @fields_xs;
        padding-right: @fields_xs;
    });

    .xxs-block({
        padding-left: @fields_xxs;
        padding-right: @fields_xxs;
    });
}

.row-offsets(){
    margin-left: (@offset_one_side * -1);
    margin-right: (@offset_one_side * -1);
}

.col-offsets(@type){
    @{type}-left: @offset_one_side;
    @{type}-right: @offset_one_side;
}

.wrapper(){
    max-width: 1140px;
    margin: 0 auto;
    .wrapper-full();
}

.row-flex(){
    display: flex;
    flex-wrap: wrap;
    .row-offsets();
}

.row-float(){
    .row-offsets();
    .clearfix();
}

.row-ib(){
    .row-offsets();
}

.col(){
    box-sizing: border-box;
    word-wrap: break-word;
    .col-offsets(margin);
}

.col-float(){
    float: left;
}

.col-ib(){
    display: inline-block;
    vertical-align: top;
}

.col-padding(){
    word-wrap: break-word;
    .col-offsets(padding);
}

.size(@n){
    @val: 100% / @columns * @n;
